.toolbar {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  background: #add8e6;
  height: 56px;
  z-index: 50;
}

.toolbar__navigation {
  height: 100%;
  display: flex;
  align-items: center;
  padding: 0 2rem;
}

