.navlink-style {
  color: white;
  text-decoration: none;
  height: 100%;
  display: flex;
  align-items: center;
  padding: 0 10px;
  font-weight: 600;
  font-size: 1.1rem;
}

.navlink-active {
  color: #3da0d8 !important;
}

.nav-logo {
  height: 30px;
  width: auto;
  padding: 0 10px;
}

.toolbar__logo {
  margin-left: 1rem !important;
  display: flex;
  align-items: center;
  font-family: "Raleway", sans-serif;
  font-size: 1.5rem;
  font-weight: 700;
  filter: drop-shadow(2px 0px 7px #ffff);
  background: -webkit-linear-gradient(#58c9e9, #3ea5dc);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  filter: drop-shadow(px 0px 5px #ffff);
}

.spacer {
  flex: 1;
}

.toolbar__navigation-items {
  ul {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
  }

  li {
    padding: 0 0.5rem;
  }
}

.nav-link__outer-logout {
  padding-right: 20px !important;
  color: white;
  text-decoration: none;
  height: 100%;
  display: flex;
  align-items: center;
  padding: 0 10px;
  font-weight: 600;
  font-size: 1.1rem;
}


.nav-link__outer {
  padding-right: 20px !important;
}

@media (max-width: 768px) {
  .toolbar__navigation-items {
    display: none;
  }
}

@media (min-width: 769px) {
  .toolbar__toggle-button {
    display: none;
  }

  .toolbar__logo {
    margin-left: 0;
  }
}
